@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.mainSection{
    display: flex;
    /* background-color: yellow; */
    justify-content: space-around;
    align-items: center;
}
.contentBox{
    width:40%;
    /* background-color: orange; */
    padding-left:1em;
}
.contentBox h1{
    font-size: 2.5rem;
    color:#301934;
    text-align: center;
}
.contentBox p{
    font-size: 1.2rem;
    margin:1em 0;
    font-family: 'Poppins', sans-serif;
}
.btnBox{
    width:100%;
    display: flex;
    justify-content: space-around;
    margin-top: 2em;;
}
.btn{
    border:2px solid transparent;
    border-radius: 2em;
    background-image: linear-gradient(#fff,#fff),radial-gradient( circle at bottom right,purple,orange);
    background-origin:border-box;
    background-clip: content-box,border-box;
}
.readMore{
    display: inline-block;
    font-size: 1.2rem;
    padding:.5em 1.5em;
    text-decoration: none;
    color:#301934;
}
.imgContainer{
    width: 50%;;
}
.imgContainer img{
    width: 90%;
    margin:0 auto;
    display: block;
    /* background-color: yellow; */
}

/* media queries */
@media screen and (max-width:900px){
html{
    font-size:14px;;
}
}
@media screen and (max-width:768px){
    html{
        font-size:14px;;
    }
    .mainSection{
       flex-direction: column;
       margin-top: 1em;;
    }
    .contentBox {
        width:90%;
        margin:0 auto;
    }
    .contentBox h1{
        font-size: 2rem;
      
    }
    .contentBox p{
        font-size: 1.1rem;
   
    }
    .imgContainer{
        width:80%;
    }
    }
    @media screen and (max-width:400px){
        .imgContainer{
            width:60%;
        }
    }
.services{
    width:100%;
    /* background-color:yellow; */
    margin:1em 0;
    display: flex;
    justify-content: space-around;
}
h1{
    text-align: center;
}
.card{
    width:25%;
    text-align: center;
    box-shadow:0 0 3px #000;
    padding:1em;
}
.serviceImg{
    width:280px;
    height: 280px;
    object-fit: contain;
}


@media screen and (max-width:990px){
    .services{
      
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    .card{
        width:40%;
      margin-bottom: 2em;
      margin-right: 1em;
    }
    .serviceImg{
        width:180px;
        height: 180px;
        object-fit: contain;
    }
}
@media screen and (max-width:768px){
    .services{
      
       flex-direction: column;
    }
    .card{
        width:70%;
    margin:1em;
    }
}
.contact{
    max-width: 990px;
    margin:2em auto;
    /* background-color: yellow; */
}
input{
    width:100%;
    padding:.7em;
    margin-bottom:1em;
    border:.3em double var(--mainColor);
    outline:none;
    font-size:1.1rem;
}
label{
    font-size:1.1rem;
    font-weight:bold
}

@media screen and (max-width:768px){
    .contact{
        width:70%;
        margin:1em auto;
    }
    input{
        width:100%;
        padding:.3em;
        border:.1em solid var(--mainColor);
    }
}
.policy{
    max-width: 990px;
    margin:2em auto;
}
li{
    font-size:1.2rem;
    font-family: 'Poppins', sans-serif;
    margin:.2em 0;
}

@media screen and (max-width:768px){
    .policy{
        width:70%;
        margin:1em auto;
    }
    li{
        font-size:1.1rem;
        
    }
}
:root{
    --mainColor:#8B008B;
}
header{
    background-color: #8B008B;
    background-color: var(--mainColor);
}
.container{
    width:95%;
    /* background-color: orange; */
    margin:0 auto;
    max-width:1200px;
}
.container-flex{
    display: flex;
    justify-content: space-between;
    align-items:center;
}
.logo{
    width:130px;
    padding:.5em 0;
}
.list{
    display: flex;
}
.listItem{
    margin-left: 2em;;
    color:#fff;
    text-decoration: none;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}
.activeItem{
    color:orange;
    border-bottom: .1em double #fff;;
}
.icon{
    color:#fff;
    font-size: 1.5rem !important;
    margin-right: 1em;
    margin-left:1.5em;
}

/* media queries */
@media screen and (max-width:1100px){
    .icon{
      
        margin-left:1em;
    }

}

@media screen and (max-width:768px){
html{
    font-size: 13px;

}
.container-flex{
    flex-direction: column;
}
.logo{
    width:150px;
}
.icon{
    margin:1em 1.5em;
}
}
@media screen and (max-width:568px){
    html{
        font-size: 11px;
    
    }
    .list{
        flex-direction: column; 
    }
    .listItem{
        margin-top: .2em;
    }
    .logo{
        width:100px;
    }
}
footer{
    position: fixed;
    bottom:0;
    background-color: #000;
    width:100%;
    padding:.5em;
}
.line{
    width:30%;
}
hr{
    margin-bottom: .5em;;
}
.icon{
    margin-right:1em;
}
.copyright p{
    text-align: center;
    color:#fff;
    margin-bottom:.5em;
}

/* media queries */
@media screen and (max-width:1024px){
    .line{
        width:40%;
    }
}
@media screen and (max-width:568px){
hr{
    display: none;
}
}
*{
   padding:0;
   margin:0;
   box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

